#AssociationsPage
{
    display: grid;
    width: 100%;
    grid-auto-rows: auto auto;
    text-align: justify;
    grid-gap: 4vmax;
    justify-content: center;
    height: 80vh;
}

@media only screen and (max-width: 767px) {

    #AssociationsPage {
        grid-template-columns: 90vw;
    }

}

@media only screen and (min-width: 768px) {
    #AssociationsPage {
        grid-template-columns: 50vw;
    }
}
