#TopPopUpPortail{
    width: 100%;
    height: 7vh;
    position: absolute;
    top:0;
    border-top-left-radius: 40px ;
    border-top-right-radius: 40px ;
}

.PopUp_BTN:hover .PopUpBTN_Wave{
    transform: scale(1.1);
}

.PopUpPortail
{
    background-color: white;
    height: 93vh;
    position: fixed;
    top: 2vh;
    z-index: 9;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 40px;
    box-shadow: 0 0 1.2vmax #8a8a8a;

}

.PopUpPortailBTN{
    cursor: pointer;
    position: relative;
    z-index: 1;
}


#logoPortail
{
    max-height: 40vh;
    max-width: 40vh;
    margin: 4vh;
}

.ButtonAssoPortail
{
    border-radius: 2vmax;
    padding: 1vmin;
    width: 6vw;
    height: 6vw;
    display:flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    box-shadow: 0 0 1vmax #dedede;
}



.close_area
{
    cursor: pointer;
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}

#closeCross
{
    position: absolute;
    top:1.5vh;
    right:4vh;
    cursor: pointer;
}

.PopUpBTN_Wave
{
    position: absolute;
    z-index: 0;
    transition: transform 0.5s ;
}

@media only screen and (max-width: 767px) {
    .PopUpBTN_Wave
    {
        width: 42vw;
        transform: translate(-20%,-15%);
    }
    #closeCross
    {
        height: 3vh;
        top:2vh
    }

    .PopUpPortailBTN
    {
        height: 12vh;
        background-color: white;
        border-radius: 30px;
        box-shadow: 0 0 1vmax #dedede;
    }

    .PopUpPortail
    {
        width: 90vw;
        left: 5vw;
    }

    .ButtonAssoPortail
    {
        width: 20vw;
        height: 20vw;
    }
    .Grid-container-PopUpPortail
    {
        display: grid;
        grid-gap: 2vmax;
        justify-content: center;
        grid-template-columns: max-content max-content max-content;
        grid-template-rows: max-content max-content  ;
        max-height: 40vh;
    }
    #Grid-container-PopUpPortailcitoyennete, #Grid-container-PopUpPortailinnovation
    {
        display: grid;
        grid-gap: 2vmax;
        justify-content: center;
        grid-template-columns: max-content max-content max-content;
        grid-template-rows: max-content max-content max-content ;
        max-height: 40vh;
        position: relative;
        top:-2vh
    }
    #Grid-container-PopUpPortailvde
    {
        position: relative;
        top:-6vh
    }
    .ButtonAssoPortail.vdeAssoPortailBTN{
        width: 9vh;
        height: 9vh;
    }
}

@media only screen and (min-width: 768px) {
    .Grid-container-PopUpPortail
    {
        display: grid;
        grid-gap: 2vmax;
        justify-content: center;
        grid-template-columns: max-content max-content max-content;
        grid-template-rows: max-content max-content  ;
        max-height: 40vh;
        bottom: 0;
    }
    #Grid-container-PopUpPortailinnovation, #Grid-container-PopUpPortailcitoyennete
    {
        display: grid;
        grid-gap: 2vmax;
        justify-content: center;
        grid-template-columns: max-content max-content max-content max-content;
        grid-template-rows: max-content max-content  ;
        max-height: 40vh;
    }
    #Grid-container-PopUpPortailvde{
        display: grid;
        grid-gap:1.5vmax;
        justify-content: center;
        grid-template-columns: max-content max-content max-content max-content max-content;
        grid-template-rows: max-content max-content  ;
        max-height: max-content;
    }
    .PopUpPortail.vdePortail{
        width: 50vw;
        left: 25vw;
    }

    .PopUpBTN_Wave
    {
        height: 37vh;
    }
    #closeCross
    {
        height: 2vmax;
    }

    .PopUpPortailBTN
    {
        height: 34vh;
    }

    .PopUpPortail
    {
        width: 40vw;
        left: 30vw;
    }

    .ButtonAssoPortail
    {
        width: 6vw;
        height: 6vw;
    }
}

